@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {

  .navbar-icon {
    @apply text-4xl md:text-5xl text-white cursor-pointer hover:text-slate-200 focus:text-slate-200
  }

  .timer-button {
    @apply shadow-md bg-white w-20 rounded-lg text-lg cursor-pointer
    hover:bg-gray-800 hover:text-white
    disabled:bg-slate-100 disabled:text-slate-500 disabled:border-slate-200 disabled:shadow-none disabled:cursor-default
    dark:bg-gunmetal dark:hover:text-slate-200 dark:hover:bg-hoverblack
    dark:disabled:bg-eerie dark:disabled:hover:text-slate-500
  }

  .setting-input {
    @apply w-16 text-center h-6 self-center dark:text-black disabled:bg-slate-100 disabled:text-slate-500 dark:disabled:bg-raisin dark:disabled:text-white/60
  }
}